<template>
	<div id="classifyContent">
		<scroll class="classifyContent-content" ref="scroll">
			<div class="right">
				<div v-for="(item,index) in contentList" :key="index" @click="gotoLink(item.link)" class="content-list">
					<img :src="item.image" class="img-content" @load="imageLoad" />
					<div class="title-content">{{item.title}}</div>
				</div>
			</div>
		</scroll>
	</div>
</template>

<script>
	import Scroll from "../../../components/common/scroll/Scroll.vue";

	export default {
		name: 'SortContent',
		components: {
			Scroll
		},
		props: {
			contentList: {
				default () {
					return []
				}
			}
		},
		data() {
			return {
				imageNumber: 0,
			}
		},
		created() {
			console.log(this.contentList);
		},
		methods: {
			imageLoad() {
				if (++this.imageNumber == this.contentList.length) {
					this.$refs.scroll.refresh();
				}
			},
			gotoLink(link) {
				window.location.href = link
			}
		},
		
		mounted() {
			this.$nextTick(function() {
				this.$on('backTop', function() {
					this.$refs.scroll.scroll.scrollTo(0, 0, 0)
				})
			})
		}
	}
</script>

<style scoped>
	#classifyContent{
		position: relative;
	}
	.right {
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-around;
		padding-left: 5px;
	}
	.classifyContent-content {
		/* overflow: hidden; */
		position: absolute;
		right: 0;
	}
	.content-list{
		position: relative;
		width: 33%;
		
	}
	.img-content {
		width: 100%;
	}
	.title-content {
		font-size: 14px;
		margin: 10px 0;
		text-align: center;
	}
</style>
