<template>
	<div id="sort">
		<nav-bar class="home-nav"><template v-slot:center>分类</template></nav-bar>
		<div class="content">
			<sort-title @getCategoryContent="getCategoryContent" />
			
				<sort-content class="classifyContent" :contentList="contentList" ref="content" />
			
		</div>

	</div>
</template>

<script>
	import NavBar from '../../components/common/navbar/NavBar.vue'
	import SortTitle from './childComps/SortTitle.vue'
	import SortContent from './childComps/SortContent.vue'
	//import Scroll from '../../components/common/scroll/Scroll.vue'
	import {
		getSubCategory
	} from '../../network/sort.js'
	export default {
		name: "Sort",
		components: {
			NavBar,
			SortTitle,
			SortContent,
			//Scroll
		},
		data() {
			return {
				contentList: []
			}
		},
		methods: {
			getCategoryContent(maitKey) {
				getSubCategory(maitKey).then(res => {
					//console.log(res);
					this.contentList = res.data.data.list
					this.$refs.content.$emit('backTop')
					//console.log(this.contentList);
				})
			}
		}
	}
</script>

<style scoped>
	.content {
		display: flex;
		font-size: 13px;
		margin-top: 5px;
	}

	.classifyTitle {
		width: 25%;
	}

	.classifyContent {
		width: 75%;
	}
</style>
