<template>
	<div>
		<scroll class="content">
			<div class="left">
				<div v-for="(item,index) in titleLists"
					:key="item.maitKey" class="classifyTitle"
					:class="{classifyTitleBefore
					:index === currentIndex}"
					@click="getCategoryContent(item,index)">
					{{item.title}}
				</div>
			</div>
		</scroll>
	</div>
</template>

<script>
	import {
		getCategoryTitle
	} from '../../../network/sort.js'
	import Scroll from "../../../components/common/scroll/Scroll.vue";
	
	export default {
		name: 'SortTitle',
		components: {
			Scroll
		},
		data() {
			return {
				titleLists: [],
				currentIndex: 0
			}
		},
		created() {
			this.getCategoryTitle()
		},
		methods: {
			getCategoryTitle() {
				getCategoryTitle().then(res => {
					//console.log(res);
					this.titleLists = res.data.data.category.list
					//console.log(this.titleLists);
				})
			},
			getCategoryContent(item, index) {
				this.currentIndex = index
				this.$emit('getCategoryContent', item.maitKey)
			}
		}
	}
</script>

<style scoped>
	.left {
		width: 100%;
		text-align: center;
	}
	.classifyTitle {
		height: 38px;
		line-height: 38px;
		font-size: 16px;
	}
	.classifyTitleBefore {
		color: red;
		border-left: 3px solid;
		font-weight: 600;
		background-color: white;
	}
	.content {
		overflow: hidden;
		height: calc(100vh - 97px);
	}
</style>
